import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
/*import { push as Menu } from 'react-burger-menu'*/
import { slide as Menu } from 'react-burger-menu'
import FullCanvas from './FullCanvas'
import AngleInput from './AngleInput'
import InputField from './InputField'
import RadioConvertUnits from './RadioConvertUnits'
//import RadioVelocityUp from './RadioVelocityUp'
import { crunchnumbers } from './crunchnumbers.js'
import arrow from './arrow.png'
import OutputMenu from './OutputMenu.js'


function convertfeetmet(value, fromunit, tounit)
{
	if(fromunit==="ft" && tounit==="m")
	{ return to_meters(value) }
	else
	{ return to_feet(value) }
}

function to_meters(feet) //defined exactly by the National Bureau of Standards.
{ return feet * 0.3048 } //Is the definition of a foot.
						 //I can't get more accurate than that.

function to_feet(meters)			//The inverse calculates to more decimals...
{ return meters * 3.2808333333 }


var defaultImperial = {
	diameter:50,
	startheight:4,
	units:"ft",
	gravity:100,
	thrownUp:3,
	accel_earth:32.174,
	angle:0,
	speed: 14, //13.894,
	percenttime:100,
}

var defaultMetric = {
	diameter: 15, //15.24,
	startheight: 1.2,  //1.219,
	units:"m",
	gravity:100,
	thrownUp: 1, //.92,
	accel_earth: 9.80665,
	angle:0,
	speed: 4, //4.235,
	percenttime: 100,	
}


//round to the nearest tenth.
function round(num, places) {
    if(places==null)
	{
		places=1;
	}
	
	var multiplier = Math.pow(10, places);
    var output = Math.round(num * multiplier) / multiplier;
	
	//fix some weird output. Probably due to feet/meters conversion not being more precise.
	if(Math.abs(output - Math.round(output)) <= .0011)
	{ output = Math.round(output); }

	return output;	
}


class App extends Component {

   constructor(props) {
      super(props);
	  
	  var defaults = defaultImperial;
	  
      this.state = {
		  diameter: defaults.diameter,
		  startheight: defaults.startheight,
		  units: defaults.units,
		  gravity: defaults.gravity,
		  thrownUp: defaults.thrownUp,
		  angle: defaults.angle,
		  speed: defaults.speed,
		  percenttime: defaults.percenttime,
		  accel_earth:defaults.accel_earth,

		  defaults: defaults,
		  g_accel: defaults.accel_earth,
		  relative_v_y: defaults.speed,
		  mode:"anglevel",
		  relative_v_x: 0,
		  standingvelocity: Math.sqrt( defaults.accel_earth * defaults.diameter/2),
		  omega: Math.sqrt( defaults.accel_earth / (defaults.diameter/2) ),

		  menuLeftOpen: false,
		  menuRightOpen: false,
		  droppedflag: false,
      }

      this.updateState = this.updateState.bind(this);
      this.setInitialState = this.setInitialState.bind(this);
      this.updateOtherVars = this.updateOtherVars.bind(this);
	  this.convertunits = this.convertunits.bind(this);
	  this.convertmode = this.convertmode.bind(this);
	  this.updateDiameter = this.updateDiameter.bind(this);
	  this.updateThrowHeight = this.updateThrowHeight.bind(this);
	  this.updateVelocity = this.updateVelocity.bind(this);
	  this.updateAngle = this.updateAngle.bind(this);
	  this.isMenuLeftOpen = this.isMenuLeftOpen.bind(this);
	  this.isMenuRightOpen = this.isMenuRightOpen.bind(this);
	  
	  //this.updateOtherVars();
	}
	
	setInitialState()
	{
	  //preserve the units, revert everything else.
	  var defaults=this.state.defaults;
	  
      this.setState({
		  diameter: defaults.diameter,
		  startheight: defaults.startheight,
		  units: defaults.units,
		  gravity: defaults.gravity,
		  thrownUp: defaults.thrownUp,
		  angle: defaults.angle,
		  speed: defaults.speed,
		  percenttime: defaults.percenttime,
		  accel_earth:defaults.accel_earth,

		  g_accel: defaults.accel_earth,
		  mode:"anglevel",
		  relative_v_y: defaults.speed,
		  relative_v_x: 0,
		  standingvelocity: Math.sqrt( defaults.accel_earth * defaults.diameter/2),
		  omega: Math.sqrt( defaults.accel_earth / (defaults.diameter/2) ),

		  droppedflag: false,
		}, ()=>{this.updateOtherVars();} )
		
	}

	componentDidMount() {
		this.updateOtherVars();
	}


   updateState(value, variable)
   {   	if(this.state[variable] !== value) //check that it's actually changed
		{								  //before needlessly running the logic
			this.setState({[variable]: value,
			}, ()=>{this.updateOtherVars();} )
		}
   }  

   convertunits(e)
   {	
		if(e.target.value !== this.state.units)
		{
			var startheight
			var diameter
			var thrownUp
			var speed

			var newdefaults
			if(this.state.units==="ft")
			{  newdefaults=defaultMetric }
			else
			{  newdefaults=defaultImperial }
			
			if(this.state.startheight===this.state.defaults.startheight)
			{ startheight=newdefaults.startheight; }
			else {  startheight=round(convertfeetmet(this.state.startheight, this.state.units, newdefaults.units)) }
			
			if(this.state.diameter===this.state.defaults.diameter)
			{ diameter=newdefaults.diameter; }
			else {  diameter=round(convertfeetmet(this.state.diameter, this.state.units, newdefaults.units)) }
	   
			if(this.state.thrownUp===this.state.defaults.thrownUp)
			{ thrownUp=newdefaults.thrownUp; }
			else {  thrownUp=round(convertfeetmet(this.state.thrownUp, this.state.units, newdefaults.units)) }

			if(this.state.speed===this.state.defaults.speed)
			{ speed=newdefaults.speed; }
			else {  speed=round(convertfeetmet(this.state.speed, this.state.units, newdefaults.units)) }
			
			this.setState( {
				startheight : startheight,  diameter : diameter, thrownUp : thrownUp, speed : speed,
				relative_v_x : round(convertfeetmet(this.state.relative_v_x, this.state.units, newdefaults.units)),
				relative_v_y : round(convertfeetmet(this.state.relative_v_y, this.state.units, newdefaults.units)),
				units: e.currentTarget.value,
				accel_earth: newdefaults.accel_earth,
				g_accel: newdefaults.accel_earth * this.state.gravity/100,
				defaults: newdefaults,
			}, ()=>{this.updateOtherVars();} )
		}
	}

   convertmode(e)  //no longer used, but kept here in case I want to revert.
   {   this.setState( {mode: e.currentTarget.value,} );   
	   
	   if(e.currentTarget.value==="thrownUp")
	   { this.setState( {relative_v_x:0,} ) }
   }   
   
   //the starting height must NEVER be greater than the diameter!
   //Thus this field gets its own handling function.
   updateDiameter(value, variable)
   {
	   if(this.state.diameter !== value)
	   {
		   if(value < this.state.thrownUp) {
			   this.setState({diameter: value,
							  startheight: value,
				}, ()=>{this.updateOtherVars();} )
		   } 
		   else if( this.state.diameter < 10 && value > 10 && this.state.startheight < 4 && this.state.units==="ft")
		   {
			   this.setState({diameter: value,
							  startheight: 4,
				}, ()=>{this.updateOtherVars();} )		   
		   }
		   else if( this.state.diameter < 2 && value > 2 && this.state.startheight < 1 && this.state.units==="m")
		   {
			   this.setState({diameter: value,
							  startheight: 1.219
	,
				}, ()=>{this.updateOtherVars();} )		   
		   }
		   else {
			   this.setState({diameter: value,
				}, ()=>{this.updateOtherVars();} )		   
		   }		   
	   }
   }
   
   updateThrowHeight(value, variable)
   {
	   if(this.state.thrownUp !== value)
	   {
			var speed = Math.sqrt( 2 * this.state.accel_earth * value );
			this.setState( {
				thrownUp: value,
				relative_v_y: speed,
				relative_v_x: 0,
				angle:0,
				speed: speed,
			}, ()=>{this.updateOtherVars();} )
	   }
   }
   
	updateVelocity(value, variable)
	{
		if(this.state.speed !== value)
		{
			var computedangle = (-1* this.state.angle * Math.PI / 180) + (Math.PI / 2);
			var droppedflag = (value===0)
			
			this.setState( {
				relative_v_x: value * Math.cos(computedangle),
				relative_v_y: value * Math.sin(computedangle),
				speed: value,
				droppedflag: droppedflag,
			}, ()=>{this.updateOtherVars();} )
		}
	}
	
	updateAngle(e)
	{
		var value = Number(e.target.value)

		if(this.state.angle !== value)
		{
			//sticky slider makes it easier to reach the defaults.
			if(e.target.type==="range" && Math.abs(value) < 5)
			{ value = 0; }
			
			var computedangle = (-1* value * Math.PI / 180) + (Math.PI / 2);

			var relative_v_x=this.state.speed * Math.cos(computedangle)
			var relative_v_y=this.state.speed * Math.sin(computedangle)
			
			if(Math.abs(relative_v_x) < 1e-2) { relative_v_x = 0; }
			if(Math.abs(relative_v_y) < 1e-2) { relative_v_y = 0; }
			
			this.setState( {
				angle: Number(value),
				relative_v_x: relative_v_x,
				relative_v_y: relative_v_y,
			}, ()=>{this.updateOtherVars();} )
		}
	}

	
	updateOtherVars()
	{
		var expectedheight
		var expectedtime
		
		if(this.state.relative_v_y > 0)
		{			
			//var expectedheight = height_start + height_thrown; //works out exactly the same for case throw "up"

			expectedheight = (Math.pow(this.state.relative_v_y,2) / (2 * this.state.accel_earth)) + this.state.startheight;
			expectedtime = Math.sqrt( 2 * expectedheight / this.state.accel_earth ) + ( this.state.relative_v_y / this.state.accel_earth)  //same formula as throw "up"
		}
		else if(this.state.relative_v_y <= 0)
		{
			expectedheight = this.state.startheight;
			
			var end_v_y = -1 * Math.sqrt( Math.pow(this.state.relative_v_y,2) + 2*this.state.accel_earth*this.state.startheight )  //temporary variable
			expectedtime = Math.abs(2 * expectedheight / (this.state.relative_v_y + end_v_y))
		}

		var g_accel = this.state.accel_earth * this.state.gravity / 100
		
		var omega = Math.sqrt( g_accel / (this.state.diameter/2) )
		var standingvelocity = Math.sqrt( this.state.g_accel * (this.state.diameter/2) )
		
		var r_oncoin = (this.state.diameter/2) - this.state.startheight
		var g_oncoin = omega*omega*r_oncoin;
		
		var standingcoin
		if(this.state.startheight < this.state.diameter/2)
		{  standingcoin = -1 * Math.sqrt( g_oncoin * r_oncoin ) } //-1 b/c station is rotating clockwise
		else
		{  standingcoin = Math.sqrt( g_oncoin * r_oncoin ) } //-1 b/c station is rotating clockwise
		
		
		var start_v_x = this.state.relative_v_x + standingcoin
		
		var answers = crunchnumbers(this.state.diameter/2, omega, start_v_x, this.state.relative_v_y, r_oncoin)
		
		var expecteddist = Math.abs(this.state.relative_v_x * expectedtime)
		var start_speed = Math.sqrt( Math.pow(start_v_x,2) + Math.pow(this.state.relative_v_y,2) )
		
		this.setState({
			expectedtime : expectedtime,
			expectedheight: expectedheight,
			standingvelocity: standingvelocity,
			omega: omega,
			g_accel: g_accel,
			maxheight:answers.maxheight,
			x_f : answers.x_f,
			y_f : answers.y_f,
			time: answers.time,
			slope: answers.slope,
			total_difference: answers.total_difference,
			directionleft: answers.directionleft,
			start_v_x: start_v_x,
			standingcoin: standingcoin,
			g_oncoin: g_oncoin,
			expecteddist: expecteddist,
			start_speed:start_speed,
			theta_traversed_person: answers.theta_traversed_person,
		})
	}
	
	isMenuLeftOpen(state) {
		this.setState( {
			menuLeftOpen: state.isOpen,
		} )
	}
	
	isMenuRightOpen(state) {
		this.setState( {
			menuRightOpen: state.isOpen,
		} )
	}
   
  render() {

  
    var defaults
	if(this.state.units==="ft")
	{	defaults = defaultImperial }
	else
	{	defaults = defaultMetric }
	
	
	var showUp = true;
	var showVelocity = false;
	var showAngle = false;
	
	if(this.state.mode==="up") {
		showUp = true;
		showVelocity = false;
		showAngle = false;
	}
	else
	{
		showUp = false;
		showVelocity = true;
		showAngle = true;
	}
	
	var fullunits
	if(this.state.units==="ft")
	{ fullunits = "feet" }	else { fullunits = "meters" }
	
	const initialwidth = { width:'initial', }
	
	var displayLeftLabel
	if(this.state.menuRightOpen)
	{ displayLeftLabel="hide" }  else  { displayLeftLabel="" }
	
	var leftButton
	if( !this.state.menuRightOpen)
	{  leftButton = "left-button"  }
	else { leftButton = "hide" }
	
	var EncapsulatedCanvas = {	slope : this.state.slope,
								time : this.state.time,
								radius: this.state.diameter/2,
								omega: this.state.omega,
								startheight: this.state.startheight,
								start_v_x: this.state.start_v_x,
								start_v_y: this.state.relative_v_y,
								units: this.state.units,
								menuLeftOpen: this.state.menuLeftOpen,
								menuRightOpen: this.state.menuRightOpen,
								percenttime: this.state.percenttime
							}

	var EncapsulatedOutput = {	rotation: this.state.omega / (2 * Math.PI),
								units: this.state.units,
								standingvelocity: this.state.standingvelocity,
								centripaccel: this.state.g_accel,
								standingcoin: -1 * this.state.standingcoin,
								g_oncoin: this.state.g_oncoin,
								time: this.state.time,
								expectedtime: this.state.expectedtime,
								maxheight: this.state.maxheight,
								expectedheight: this.state.expectedheight,
								finalseparation: this.state.total_difference,
								expecteddist: this.state.expecteddist,
								throwspeed: this.state.start_speed,
								theta_traversed_person: this.state.theta_traversed_person,
								
								//need to know whether the coin landed to the right or left of the person.
								relative_v_x: this.state.relative_v_x,
								directionleft: this.state.directionleft,
							}
	//pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" }  //Menu props only necessary for other menu types.

    return (
    
<div className="App" id="outer-container">

	<Menu burgerButtonClassName={ leftButton } menuClassName={ "bm-menu-left" } onStateChange={ this.isMenuLeftOpen }  >
	  <div id="CenteredLeftMenu">
		<div><h3>Input Values</h3></div>
		<RadioConvertUnits onChange={this.convertunits} units={this.state.units} />

		<InputField title="Height thrown up on Earth" value={this.state.thrownUp} min={0} updateState={this.updateThrowHeight} variable="thrownUp" units={this.state.units} default={defaults["thrownUp"]} display={showUp} />

		<InputField title="Starting height" min={0} max={this.state.diameter} updateState={this.updateState} value={this.state.startheight} default={defaults["startheight"]} variable="startheight" units={this.state.units}/>

		<AngleInput title="Angle from vertical" value={this.state.angle} min="-180" max="180" updateState={this.updateAngle} variable="angle" units="" droppedflag={this.state.droppedflag} display={showAngle} />		
		
		<InputField title="Throw speed" value={this.state.speed} min={0} max={(this.state.standingvelocity+10) * 68} updateState={this.updateVelocity}  default={defaults["speed"]}  variable="speed" units={this.state.units+"/s"} display={showVelocity} />
		
		<table><tbody><tr><td><div className="randomFact">Height coin reaches<br/>if thrown on Earth</div></td><td><div className="UnitsStyle" style={initialwidth}>{round(this.state.expectedheight)}&nbsp;{this.state.units}</div></td></tr></tbody></table>

		<InputField title="Station diameter" value={this.state.diameter} min={1} max={10000000000}  default={defaults["diameter"]} updateState={this.updateDiameter} variable="diameter" units={this.state.units}/>
		
		<InputField title="Gravity at floor" min={0} max={2000} updateState={this.updateState} variable="gravity"  default={defaults["gravity"]} value={this.state.gravity} units="%"/>

		<InputField title="Playback speed" min={0} max={Math.ceil(this.state.time)*100} updateState={this.updateState} default={defaults["percenttime"]}  variable="percenttime" value={this.state.percenttime} units="%"/>

		<ResetButton updateState={this.setInitialState}/>
	  </div>
	</Menu>

	<Menu right burgerButtonClassName={ "right-button" } menuClassName={ "bm-menu-right" } onStateChange={ this.isMenuRightOpen } ><OutputMenu v={EncapsulatedOutput}></OutputMenu></Menu>

	<div id="page-wrap">
		<div className="navbar">A Coin Toss on a Virtual Space Station</div>
		
		<div id="canvasWrapper" ><FullCanvas v={EncapsulatedCanvas} defaults={defaults} /></div>
			
		<div className="bottom">
			<div id="scaleunits"><i>Scale in {fullunits}</i></div>
			
			<a href="https://priceisright204.github.io/coriolis.html">
				<div className="referenceLink">What is this page?</div></a>

			<div className="smallBreak"><br/></div>
			<div className="bubble">
				<div>This is the <b><i>thrower's</i></b> perspective.<br/>The floor looks stationary to him.<br/>
				Through a window, he sees <b>outer space</b> rotating counterclockwise.</div>
					<img src={arrow} alt="curved-arrow" height="25" width="100"/>
			</div>
		</div>

		<div id="inputlabel" className={displayLeftLabel}>Input</div>
		<div id="outputlabel">Output</div>
	</div>
	
</div>

    );
  }
}

//	<RadioVelocityUp onChange={this.convertmode} units={this.state.mode} /> //no longer used


class ResetButton extends Component {
	render() {
		
	return (
		<button type="submit" className="ResetButton" onClick={ this.props.updateState }>Reset</button>
		)
  }
}

export default App;



