/*eslint-disable eqeqeq*/

import React, { Component } from 'react';
import './App.css';

class OutputMenu extends Component {
	
	constructor(props) {
		super(props);
		
		this.state={ units: this.props.v.units + "/s", }
		this.convertunits = this.convertunits.bind(this);
	}
	
	convertunits(e)
	{	this.setState( { units: e.currentTarget.value, }) }

	
	componentWillUpdate(nextProps, nextState) {
		if(this.props.v.units !== nextProps.v.units)
		{	this.setState( { units: nextProps.v.units + "/s", });  }
	}
	
	
	render() {
		
		//Pick the right units for rotation speed
		var rotation = this.props.v.rotation;
		var rotation_units = "rev/s" //not likely. But I validate below.

		if(rotation < 1)
		{	rotation_units = "rpm"
			rotation = rotation * 60; }
			
		if(rotation < 1)
		{	rotation_units = "rev/h"
			rotation = rotation * 60;	}
			
		if(rotation < 0.5)
		{	rotation_units = "rev/d"
			rotation = rotation * 24;
		}
		
		var units2
		if(this.props.v.units==="ft")
		{ units2="mph" }
		else if(this.props.v.units==="m")
		{ units2="km/h" }
		
		var speedunits = this.state.units
		var standingvelocity = this.props.v.standingvelocity
		var standingcoin = Math.abs(this.props.v.standingcoin)
		var throwspeed = this.props.v.throwspeed
		
		//other speed formats
		if(speedunits==="mph")
		{ standingvelocity *= 0.681818; standingcoin *= 0.681818; throwspeed *= 0.681818; }
		if(speedunits==="km/h")
		{ standingvelocity *= 3.6; standingcoin *= 3.6; throwspeed *= 0.681818; }

	
		//decide to display cm/inches
		var conditionalbreak = {}
		var finalseparation2 = ""
		var finalseparationunits2 = ""
		var expecteddist2 = ""
		if(this.props.v.finalseparation < 1.0)
		{	
			if(this.props.v.units=="ft")
			{
				finalseparation2 = this.props.v.finalseparation * 12;
				finalseparation2 = "(" + format(finalseparation2);
				finalseparationunits2 = "in)";
				
				expecteddist2 = this.props.v.expecteddist * 12;
				expecteddist2 = "(" + format(expecteddist2);
			}
			
			if(this.props.v.units=="m")
			{
				finalseparation2 = this.props.v.finalseparation * 100
				finalseparation2 = "(" + format(finalseparation2);
				finalseparationunits2 = "cm)";

				expecteddist2 = this.props.v.expecteddist * 100
				expecteddist2 = "(" + format(expecteddist2);
			}
		} else { conditionalbreak = { display:'none', } }

		
		var showDifference
		var difference
		if(this.props.v.expecteddist !== 0)
		{	showDifference=""
			//handles the unusual (but important) case of the coin landing on the opposite side of where you threw it.
			if( (this.props.v.relative_v_x > 0 && this.props.v.directionleft) ||
				(this.props.v.relative_v_x < 0 && !this.props.v.directionleft) )
			{	difference = Math.abs(this.props.v.finalseparation+this.props.v.expecteddist)	}
			else
			{  difference = Math.abs(this.props.v.finalseparation-this.props.v.expecteddist)	}
		} else { showDifference="hide" }
	
		//If the person has traveled more than half a circle,
		//display the number of rotations and
		//display a warning.
		var warning="hide"
		var rotationAngle = ""
		var numRotations = "hide"
		if(this.props.v.theta_traversed_person > Math.PI )
			{ warning = "distortionwarning" 
			  numRotations=""
			  rotationAngle="hide" }	

		return( 
		
<div id="outputbox"><h3>Output Values</h3>

<div id="outeroutputtable">
<table id="innertable"><tbody>
	<tr className="border_bottom"><td><h3>Results</h3></td>
		<td className="redcolor">On station</td>
		<td className="bluecolor">On Earth</td>
	</tr>
		<tr>
			<td className="borderright">Time in the air</td>
			<td><div id="timeinair" className="output1">{format(this.props.v.time)}</div>
				<div id="timeinairunits" className="output1">&nbsp;s</div></td>
			<td><div id="expectedtime" className="output1">{format(this.props.v.expectedtime)}</div>
				<div id="expectedtimeunits" className="output1">&nbsp;s</div></td>
		</tr>
		<tr>
			<td className="borderright">Max height reached</td>
			<td><div id="maxheightachieved" className="output1">{format(this.props.v.maxheight)}</div>
				<div id="maxheightachievedunits" className="output1">&nbsp;{this.props.v.units}</div></td>
			<td><div id="expectedheight" className="output1">{format(this.props.v.expectedheight)}</div>
				<div id="expectedheightunits" className="output1"></div>&nbsp;{this.props.v.units}</td>
		</tr>
		<tr id="boldrow">
			<td className="borderright"><div className="redcolor"><div>Distance away</div> at landing</div></td>
			<td>
				<div id="finalseparation" className="output1">{format(this.props.v.finalseparation)}</div>
					<div id="finalseparationunits" className="output1">&nbsp;{this.props.v.units}</div><div style={conditionalbreak}></div>
				<div id="finalseparation2" className="output1">{finalseparation2}</div>
					<div id="finalseparationunits2" className="output1">&nbsp;{finalseparationunits2}</div>
				</td>
			<td>
				<div id="expecteddist" className="output1">{format(this.props.v.expecteddist)}</div>
					<div id="expecteddistunits" className="output1">&nbsp;{this.props.v.units}</div><div style={conditionalbreak}></div>
				<div id="expecteddist2" className="output1">{expecteddist2}</div>
					<div id="expecteddistunits2" className="output1">&nbsp;{finalseparationunits2}</div>
				<div id="" className="output1"></div>
					<div id="" className="output1"></div>
				</td>
		</tr>
</tbody></table></div>

<div className={warning}>High level of distortion</div>

<div id="leftradio"><RadioOutputUnits value1={this.props.v.units +"/s"} value2={units2} selected={this.state.units} onChange={this.convertunits} /></div>

<table id="secondtable"><tbody>

<tr className={ showDifference } ><td><div className="leftoutput">Difference from expected landing:</div></td>
	<td>{format(difference)}</td>
	<td>{this.props.v.units}</td>
</tr>

<tr><td><div className="leftoutput">Rotation rate (station):</div></td>
	<td>{format(rotation)}</td>
	<td>{rotation_units}</td>
</tr>

<tr className={rotationAngle}><td><div className="leftoutput">Angle the person has rotated:</div></td>
	<td>{format(this.props.v.theta_traversed_person * 180 / Math.PI)}</td>
	<td></td>
</tr>

<tr className={numRotations}><td><div className="leftoutput">Rotations the person has completed:</div></td>
	<td>{format(this.props.v.theta_traversed_person / (2 * Math.PI))}</td>
	<td>rev</td>
</tr>


<tr><td><div className="leftoutput">Floor speed:</div></td>
	<td>{format(standingvelocity)}</td>
	<td>{speedunits}</td>
</tr>

<tr><td><div className="leftoutput">Coin's speed before throw:</div></td>
	<td>{format(standingcoin)}</td>
	<td>{speedunits}</td>
</tr>

<tr><td><div className="leftoutput">Coin's speed after throw:</div></td>
	<td>{format(throwspeed)}</td>
	<td>{speedunits}</td>
</tr>


<tr><td><div className="leftoutput">Acceleration at floor:</div></td>
	<td>{format(this.props.v.centripaccel)}</td>
	<td>{ this.props.v.units + "/s/s" }</td>
</tr>

<tr><td><div className="leftoutput">Acceleration on coin before throw:</div></td>
	<td>{format(this.props.v.g_oncoin)}</td>
	<td>{ this.props.v.units + "/s/s" }</td>
</tr>
</tbody></table>
			
</div>

		);

		
	}
}


function format(num)
{
	num = round(num)
	
	if(!isFinite(num) )
	{ return "" }
	else
	{ return num.toString() }
	
}

//rounds to four significant figures.
function round(num, places) {
	if(num===0)
	{ return 0 }
	
    if(places==null && num > 1)
	{
		var order = Math.floor(Math.log(num) / Math.LN10
                       + 0.000000001); // because float math sucks like that
		places = (order * -1) + 3
	}
	else if(places==null)
	{ places = 2 }
	
	var multiplier = Math.pow(10, places);
    var output = Math.round(num * multiplier) / multiplier;
	
	//fix some weird output. Probably due to feet/meters conversion not being more precise.
	if(Math.abs(output - Math.round(output)) <= .0011)
	{ output = Math.round(output); }

	return output;	
}


class RadioOutputUnits extends Component {
	
	render(){

	return (
		<form><table><tbody>
		
		<tr>
			<td>
				<input type="radio" name="units3" value={this.props.value1} onChange={this.props.onChange} checked={this.props.value1 === this.props.selected} /></td>
				<td>Speed in {this.props.value1}</td><td>&#160;&#160;</td>
		
			<td>
				<input type="radio" name="units4" value={this.props.value2}  onChange={this.props.onChange} checked={this.props.value2 === this.props.selected} /></td>
				<td>Speed in {this.props.value2}</td>
		</tr>
				
		</tbody></table></form>
	)}
}


export default OutputMenu;



